import React from 'react'
import { Helmet } from "react-helmet"

const SEO = () => {
    return (
        <div>
            <Helmet 
                htmlAttributes={{
                    lang: 'en',
                }}
            >
                <title>FindHouse - Real Estate HTML Template</title>
                <meta name="description" content="FindHouse - Real Estate HTML Template" />
                <meta name="og:title" property="og:title" content="FindHouse - Real Estate HTML Template"></meta>
                <meta name="twitter:card" content="FindHouse - Real Estate HTML Template"></meta>
                <link rel="canonical" href="https://rewy-react.envytheme.com/"></link>
                <meta property="og:image" content="https://res.cloudinary.com/dev-empty/image/upload/v1593069801/explore-learning.jpg" />
            </Helmet>
        </div>
    )
}

export default SEO