import React , {useEffect, useState} from 'react'
import { Link ,graphql, useStaticQuery} from 'gatsby'
import SEO from './SEO'
import GoTop from './GoTop'
import MobileNav from '../App/Navbar/MobileNav'

const Layout = ({ children }) => {
    const data = useStaticQuery(graphql`
		query{
			logo2: file(relativePath: {eq: "header-logo2.png"}) {
				id
				childImageSharp {
				  fluid {
					originalImg
				  }
				}
			  }
		}`
    )
    const [open, setOpen] = useState(false);
    useEffect(()=>{
        let menu = document.getElementById("menu");
        let html = document.querySelector('html')
        let mm0 = document.getElementById('mm-0')
            if(open===true){
            html.classList.add('js', 'touch', 'csstransitions')
                    menu.classList.add('mm-menu_opened');
                    html.classList.add('mm-wrapper_opened', 'mm-wrapper_blocking', 'mm-wrapper_background', 'mm-wrapper_opening')
                    mm0.style.minHeight="310px"
                    console.log('clicked')
            }else{
                menu.classList.remove('mm-menu_opened');
                    menu.setAttribute('aria-hidden', 'true')
                    html.classList.remove('mm-wrapper_opened', 'mm-wrapper_blocking', 'mm-wrapper_background', 'mm-wrapper_opening')
                    mm0.style.minHeight=""
            }
    },[open])

    
    return (
        <>
            <SEO />
            <div id='___loader' className='preloader'></div>
           <nav id="menu" className="stylehome1 mm-menu mm-menu_offcanvas">
            <MobileNav/>
		    </nav>
            <div className="wrapper mm-page mm-slideout" id="mm-0">
                <div id="page" className="stylehome1 h0">
                    <div className="mobile-menu" id='mobile-nav'>
                        <div className="header stylehome1">
                            <div className="main_logo_home2 text-center">
                                <img className="nav_logo_img img-fluid mt20" src={data.logo2.childImageSharp.fluid.originalImg} alt="header-logo2.png"/>
                                <span className="mt20">FindHouse</span>
                            </div>
                            <ul className="menu_bar_home2">
                                <li className="list-inline-item list_s"><Link to="/register/"><span className="flaticon-user"></span></Link></li>
                                <li className="list-inline-item"><Link to="#menu"  onClick={()=>setOpen(!open)}><span></span></Link></li>
                            </ul>
                        </div>
                    </div>
            </div>

            {children}
            </div>
            <GoTop />
        </>
    )
}

export default Layout
